* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: var(--light-green-color);
    padding: 0rem 2rem;
}

:root {
    --light-green-color: #A5B68D;
    --dark-green-color: #859471;
    --brown-color: #B17F59;
    --gray-color: #4F4F4F;
    --beige-color: #EDE8DC;
    --white-color: #FFFFFF;
    --font-family: "Unbounded";
    --font-weight-black: 800;
    --font-weight-medium: 400;
}

.main {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    height: 80vh;
}

.main h1 {
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 0.75rem;
    color: var(--beige-color);
}

.tableWrapper {
    background-color: var(--beige-color);
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    padding: 1.5rem 2rem;
    width: 100%;
}

.tableWrapper tr,
.tableWrapper td {
    font-family: var(--font-family);
    font-weight: 200;
    font-size: 0.75rem;
    color: var(--gray-color);
}

.tableWrapper tr {
    font-weight: var(--font-weight-black);
}

.buttonToLogin {
    width: 100%;
}

.buttonToLogin button {
    background-color: var(--dark-green-color);
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 1rem;
    color: var(--beige-color);
    width: 100%;
    padding: 0.5rem;
}