* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: var(--light-green-color);
    padding: 0rem 3rem;
}

:root {
    --light-green-color: #A5B68D;
    --dark-green-color: #859471;
    --brown-color: #B17F59;
    --gray-color: #4F4F4F;
    --beige-color: #EDE8DC;
    --white-color: #FFFFFF;
    --font-family: "Unbounded";
    --font-weight-black: 800;
    --font-weight-medium: 400;
}

.main {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    height: 80vh;
}

.main h1 {
    position: absolute;
    bottom: 90%;
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 0.75rem;
    color: var(--beige-color);
}

.main h2 {
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 0.75rem;
    color: var(--beige-color);
}

article {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
}

.question {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0.25rem;
}

.math {
    background-color: var(--beige-color);
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    padding: 0.5rem 2rem;
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 1rem;
    color: var(--gray-color);
    width: 100%;
}

.equal {
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 1rem;
    color: var(--beige-color);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
}

.answer input {
    background-color: var(--beige-color);
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    padding: 0.5rem 1rem;
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 1rem;
    color: var(--gray-color);
    width: 100%;
}

.checkAnswer {
    background-color: var(--dark-green-color);
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    padding: 0.5rem;
    width: 100%;
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 1rem;
    color: var(--beige-color);
}

.message {
    position: absolute;
    bottom: 45%;
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 1rem;
    color: var(--white-color);
}

.counterCorrAnsw {
    position: absolute;
    bottom: 10%;
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 0.75rem;
    color: var(--white-color);
    opacity: 0.5;
}
