* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: var(--light-green-color);
    padding: 0rem 5rem;
}

:root {
    --light-green-color: #A5B68D;
    --dark-green-color: #859471;
    --brown-color: #B17F59;
    --gray-color: #4F4F4F;
    --white-color: #FFFFFF;
    --font-family: "Unbounded";
    --font-weight-black: 800;
    --font-weight-medium: 400;
}

.main {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    justify-content: center;
    align-items: center;
    height: 80vh;
}

.main h2 {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 0.75rem;
    color: var(--white-color);
}

.buttonsLevel {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.buttonsLevel button:nth-child(1) {
    padding: 0.5rem 2rem;
    border-radius: 20px;
    border: none;
    background-color: var(--light-green-color);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    font-family: var(--font-family);
    font-weight: 200;
    font-size: 1rem;
    color: var(--white-color);
}

.buttonsLevel button:nth-child(2) {
    padding: 0.5rem 2rem;
    border-radius: 20px;
    border: none;
    background-color: var(--dark-green-color);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1rem;
    color: var(--white-color);
}

.buttonsLevel button:nth-child(3) {
    padding: 0.5rem 2rem;
    border-radius: 20px;
    border: none;
    background-color: var(--brown-color);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 1rem;
    color: var(--white-color);
}