* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: var(--light-green-color);
}

:root {
    --light-green-color: #A5B68D;
    --dark-green-color: #859471;
    --brown-color: #B17F59;
    --gray-color: #4F4F4F;
    --white-color: #FFFFFF;
    --font-family: "Unbounded";
    --font-weight-black: 800;
    --font-weight-medium: 400;
}

.main {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    justify-content: center;
    align-items: center;
    height: 80vh;
    padding: 0rem 3rem;
}

.nameContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.nameContainer h1 {
    font-family: var(--font-family);
    font-weight: 200;
    font-size: 0.75rem;
    color: var(--white-color);
}

.nameContainer input {
    padding: 0.5rem 3rem;
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 1rem;
    color: var(--gray-color);
    width: 100%;
}

.enterButton {
    width: 100%;
}

.enterButton button {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 1rem;
    color: var(--white-color);
    background-color: var(--dark-green-color);
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    width: 100%;
    padding: 0.5rem;
}

